.bg-container{
    display: flex;
    flex-direction: row-reverse;
    @include telefono{
        display: block;
    }
    .bg{
        width: 50vw;
        position: absolute;
        @include telefono{
            display: none;
        }
    }
    .azul{
        background-color: $colorPrincipal;
        height: 100%;
    }
}
.login-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    @include telefono{
        display: block;
    }
}
.formulario{
    margin-bottom: calc($separacion / 2);
    padding-right: calc($separacion * 2);
    @include telefono{
        margin: 0;
        padding: 0;
    }
    .formulario-title{
        margin-bottom: 1.3rem;
    }
    .form{
        .campo{
            display: flex;
            flex-direction: column;
            .label{
                font-weight: $bold;
                font-size: 1.6rem;
                margin-top: 2rem;
            }
            .input{
                margin: calc($separacion / 4) 0 0 0;
                height: calc($separacion / 1);
                border-radius: 0.5rem;
                border: 2px solid $border;
                font-size: 1.6rem;
                padding: 0 2rem;
            }
        }
        .checkbox{
            flex-direction: row-reverse;
            align-items: center;
            column-gap: calc($separacion / 5);
            margin-bottom: calc($separacion / 1.5);
            input[type=checkbox] {
                transform: scale(1.3);
                border: 0px solid #000;
            }
            .label-checkbox{
                font-weight: $medium;
                .terms-dec{
                    color: $colorPrincipal;
                    font-weight: $bold;
                    border-bottom: 2px solid $colorPrincipal
                }
            }
            .square{
                border: 1px solid $border;
            }
        }
        .submit{
            border: none;
            @include btn;
            width: 100%;
            font-size: 1.6rem;
        }
        .signin{
            text-align: center;
            margin-top: 1rem;
            @include telefono{
                margin-bottom: 2rem;
            }
            .signin-text{
                color: $colorPrincipal;
            }
        }
    }
}
